<template>
  <div class="account">
    <!-- 导航栏 -->
    <van-nav-bar
      title="个人中心"
      left-text="返回"
      left-arrow
      @click-left="back"
    />
    <div class="content">
      <van-cell title="用户名" v-model="accountInfo.userId"/>
      <van-cell title="手机号" v-model="accountInfo.phone"/>
      <van-cell title="昵称">
        <template #extra>
          <input type="text"  v-model="accountInfo.nickName" @change="updateUserInfo('nickName','/updateNickName')">
        </template>
      </van-cell>
      <van-cell title="头像" center>
        <template #extra>
          <div class="img-box">
            <van-uploader :after-read="updateAvatar" />
           <img class="auto-img" :src="accountInfo.userImg">
          </div>
        </template>
      </van-cell>
      <van-cell title="简介" label-class="label">
        <template #label>
          <input type="text"  v-model="accountInfo.desc" @change="updateUserInfo('desc','/updateDesc')">
        </template>
      </van-cell>
    </div>
  </div>
</template>

<script>
  import "../assets/less/account.less"
  import {utils} from "../assets/js/utils"
  export default {
    data(){
      return{
        accountInfo: {},
        maxSize: 1 * 1024 * 1024,
      }
    },
    created(){
      this.getUserData();
    },
    methods:{
      back(){
        this.$router.go(-1);
      },
      // 获取用户数据
      getUserData(){
        let tk = localStorage.getItem('CSTK');
        // 不存在token
        if(!tk){
          return;
        }
        
        this.$toast.loading({
          message: '加载中...',
          forbidClick: true,
          duration: 0,
          loadingType: 'spinner'
        })

        this.axios({
          method: "GET",
          url: "/findAccountInfo",
          params: {
            appkey: this.appkey,
            tokenString: tk
          }
        }).then( result => {
          this.$toast.clear();
          if(result.data.code == "B001"){
            this.accountInfo = result.data.result[0];
          }
          // console.loglog("findAccountInfo result ==> ",result)
        }).catch( err => {
          this.$toast.clear();
          // console.loglog("findAccountInfo err ==> ",err)
        })
      },
      updateAvatar(file){
        let tk = localStorage.getItem('CSTK');
        // 不存在token
        if(!tk){
          return;
        }

        // console.loglog("file ==> ",file)

        // 限制类型
        let imgTypes = ['png','jpg','jpeg','gif'];
        let type = file.file.type.split('/')[1];
        if(imgTypes.indexOf(type) === -1){
          this.$toast(`图片格式只支持${imgTypes.join(',')}`)
          return;
        }

        // 限制大小
        if(file.file.size > this.maxSize){
          this.$toast(`上传图片大小要小于${this.maxSize / 1024}kb`);
          return;
        }

        // // console.loglog("type ==> ",type)
        let base64 = file.content.replace(/data:image\/[A-Za-z]+;base64,/,"");
        
        this.$toast.loading({
          message: '加载中...',
          forbidClick: true,
          duration: 0,
          loadingType: 'spinner'
        })

        let data = utils.queryString({
          appkey: this.appkey,
          tokenString: tk,
          imgType: type,
          serverBase64Img: base64
        })

        this.axios({
          method: "POST",
          url: "/updateAvatar",
          data
        }).then( result => {
          this.$toast.clear();
          if(result.data.code == "H001"){
            this.accountInfo.userImg = result.data.userImg;
            this.$toast(result.data.msg);
          }
          // console.loglog("updateAvatar result ==> ",result)
        }).catch( err => {
          this.$toast.clear();
          // console.loglog("updateAvatar err ==> ",err)
        })
      },
      updateUserInfo(key,url){
        let tk = localStorage.getItem('CSTK');
        // 不存在token
        if(!tk){
          return;
        }
        
        this.$toast.loading({
          message: '加载中...',
          forbidClick: true,
          duration: 0,
          loadingType: 'spinner'
        })

        // console.loglog(this.accountInfo[key])

        let data = utils.queryString({
          appkey: this.appkey,
          tokenString: tk,
          [key]: this.accountInfo[key]
        })

        // console.loglog(data)

        this.axios({
          method: "POST",
          url,
          data
        }).then( result => {
          this.$toast.clear();
          this.$toast(result.data.msg);
          // if(result.data.code == "C001"){
          // }
          // console.loglog( url, "result ==> ",result)
        }).catch( err => {
          this.$toast.clear();
          // console.loglog( url, "err ==> ",err)
        })      

      }
    },
  }
</script>
